<template>
  <div id='box'>
    <div class="list">
      <div class="item" v-for="(item,i) in data" :key="i">
        <div class="top pos">
            <div class="left">
              <img src="@/assets/images/my/1.png" alt="" class="img">
              <span>{{item.pay_time}}</span>
            </div>
            <span :class="item.status=='0'?'light':'type'">{{item.status=="0"?"已完成":'使用中'}}</span>
        </div>
        <div class="bottom">
          <div>
            <p class="num"><i style="color:#15BDDB;">•&nbsp;</i> 机器编号：{{item.number}}</p>
            <p><i style="color:#15BDDB;">•&nbsp;</i> 使用时长：{{item.use_time}}分钟</p>
          </div>
          <div class="right" :class="item.status=='0'?'light':'type'"><span class="fh">￥</span>{{item.pay_price}}</div>
        </div>
      </div>
    </div>
    <div style="color:#aaa;text-align:center;">~ 已经到底了 ~</div>
  </div>
</template>

<script>
import { getzujiejilu } from '@/axios/getData'
// import { List } from 'vant'
export default {
  components: {

  },
  props: {
    
  },
  data() {
    return {
      data:[],
    };
  },
  computed: {

  },
  created() {
    this.getdata();
  },
  methods: {
    async getdata(){
      var res = await getzujiejilu();
      this.data = res.data
      // console.log(res.data)
    }
  },
  mounted() {

  },
};
</script>

<style scoped lang="scss">
#box{
  height:100vh;
  background: #FAFAFA;
}
  .list{
    padding:20pt;
    .item{
      margin-bottom:50px;
      background: #FFF;
      border-radius: 6pt;
      box-shadow:0px 0px 10px rgba(167, 165, 165, 0.5);
      .top{
        padding:5%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .img{
          margin-right:10pt;
          width:15pt;
          height:15pt;
        }

        .left{
          display: flex;
          align-items: center;
        }
      }
      .pos{
        position: relative;
      }
      .pos::after{
        position: absolute;
        display: block;
        content:"";
        left:5%;
        width:90%;
        bottom:2pt;
        border-bottom:1pt dashed #D7D7D7;
      }
      .bottom{
        padding:5%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .right{
          font-weight: 600;
          font-size: 18pt;
          .fh{
            font-size: 9pt;
            font-weight: 800;
          }
        }
      }
    }
  }
  /* 使用中 */
.type{
  color:#25E98A;
}
.light{
  color:#999;
}
.num{
  display: inline-block;
  padding-bottom:5pt;
}
</style>



